import {
    onMounted
} from "vue";
export default function () {
    onMounted(() => {
        var clocks = document.getElementsByClassName("particleClock");
        var ctx = [];
        var timestr = "";
        var particleArray = [];
        var colors = [];

        var fonts = [];

        function init() {

            for (var i = 0; i < clocks.length; i++) {
                //规定每个数字的宽度
                clocks[i].width = 150;
                clocks[i].height = 250;
                ctx[i] = clocks[i].getContext("2d");
                var gradientSec = ctx[i].createLinearGradient(0, 0, 170, 0);
                gradientSec.addColorStop("0", "#ff9f1a");
                gradientSec.addColorStop("0.5", "skyblue");
                gradientSec.addColorStop("1.0", "pink");
                var gradientMin = ctx[i].createLinearGradient(0, 0, 200, 0);
                gradientMin.addColorStop("0", "#ff6b81");
                gradientMin.addColorStop("0.5", "#eccc68");
                gradientMin.addColorStop("1.0", "#1e90ff");
                var gradientHour = ctx[i].createLinearGradient(0, 0, 170, 170);
                gradientHour.addColorStop("0", "#4b7bec");
                gradientHour.addColorStop("0.5", "#a55eea");
                gradientHour.addColorStop("1.0", "#fc5c65");
                colors = [
                    gradientHour,
                    gradientHour,
                    gradientMin,
                    gradientMin,
                    gradientSec,
                    gradientSec,
                ];
                ctx[i].fillStyle = colors[i];
                particleArray.push([]);
            }
            for (var i = 0; i < 10; i++) {
                ctx[0].font = "15px Arial";
                //得到字体宽度取整
                var width = ctx[0].measureText(i).width | 0;
                //fillText(text,相对画布的x,y)，在画布上写文本
                ctx[0].fillText(i, 0, 25);
                //getImageData() 方法返回 ImageData 对象，该对象拷贝了画布指定矩形的像素数据。
                var data = ctx[0].getImageData(0, 0, width, 40).data;
                var len = data.length;
                var tdata = [];
                for (var j = 0; j < len / 4; j++) {
                    if (data[j * 4 + 3] != 0) {
                        var x = j % width | 0;
                        var y = (j / width) | 0;
                        tdata.push([x, y]);
                    }
                }
                fonts.push(tdata);
                ctx[0].clearRect(0, 0, 40, 40);
            }
        }

        function timestring() {
            var d = new Date();
            return (
                ("0" + d.getHours()).slice(-2) +
                ("0" + d.getMinutes()).slice(-2) +
                ("0" + d.getSeconds()).slice(-2)
            );
        }
        init();
        class particle {
            constructor(x, y) {
                this.x = Math.random() * 25 - 2.5;
                this.y = Math.random() * 40 + 5;
                this.ex = x;
                this.ey = y;
            }
            update() {
                this.x -= (this.x - this.ex) / 10;
                this.y -= (this.y - this.ey) / 10;
            }
            draw(i) {
                ctx[i].fillStyle = colors[i];
                ctx[i].beginPath();
                ctx[i].arc(this.x * 10 + 25, this.y * 10 - 50, 2, 0, Math.PI * 180, 0);
                ctx[i].fill();
            }
        }

        function change(ind, pi) {
            var newcount = fonts[ind].length - particleArray[pi].length;
            if (newcount >= 0) {
                for (var i = 0; i < newcount; i++) {
                    particleArray[pi].push(new particle(10, 20));
                }
            }
            if (newcount < 0) {
                particleArray[pi].splice(0, -newcount);
            }
            for (var i = 0; i < fonts[ind].length; i++) {
                particleArray[pi][i].ex = fonts[ind][i][0];
                particleArray[pi][i].ey = fonts[ind][i][1];
            }
            particleArray[pi].sort(function (a, b) {
                return Math.random() - 0.5;
            });
        }

        function draw() {
            var timer = timestring();
            for (var i = 0; i < timer.length; i++) {
                if (timer.charAt(i) != timestr.charAt(i)) {
                    change(timer.charAt(i), i);
                }
            }
            timestr = timer;
            for (var i = 0; i < clocks.length; i++) {
                ctx[i].fillStyle = "rgba(0,0,0,0.1)";
                ctx[i].fillRect(0, 0, 250, 400);
                for (var j = 0; j < particleArray[i].length; j++) {
                    let p = particleArray[i][j];
                    p.update();
                    p.draw(i);
                }
            }
            requestAnimationFrame(draw)
        }
        draw()
    })

}