import {
  onMounted
} from "vue";

export default function () {
  onMounted(() => {
    let canvas = document.getElementById("canvas");
    canvas.width = 200;
    canvas.height = 200;
    //绘制需要获取上下文对象
    let ctx = canvas.getContext("2d");
    let clock = document.getElementById("clock");
    //createLinearGradient() 方法创建线性的渐变对象。
    var gradientSec = ctx.createLinearGradient(0, 0, 170, 0);
    gradientSec.addColorStop("0", "magenta");
    gradientSec.addColorStop("0.5", "blue");
    gradientSec.addColorStop("1.0", "red");
    var gradientMin = ctx.createLinearGradient(0, 0, 200, 0);
    gradientMin.addColorStop("0", "#ff6b81");
    gradientMin.addColorStop("0.5", "#eccc68");
    gradientMin.addColorStop("1.0", "#1e90ff");
    var gradientHour = ctx.createLinearGradient(0, 0, 170, 170);
    gradientHour.addColorStop("0", "#4b7bec");
    gradientHour.addColorStop("0.5", "#a55eea");
    gradientHour.addColorStop("1.0", "#fc5c65");
    let colors = [gradientHour, gradientMin, gradientSec];

    function draw() {
      //clearRect(矩形的左上角x坐标，y坐标，矩形宽，高) 方法删除一个画布的矩形区域。
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      let d = new Date();
      //secdeg秒针当前角度
      //60s旋转周每秒6deg + getMilliseconds()根据当地时间返回时间的毫秒，让秒针过渡更加平滑。
      let secdeg = d.getSeconds() * 6 + (d.getMilliseconds() * 6) / 1000;
      //分针同理
      let mindeg = d.getMinutes() * 6 + secdeg / 60;
      //时针通过取余转为12时计时法，12h旋转一周每时30deg
      let hourdeg = (d.getHours() % 12) * 30 + mindeg / 60;
      let lines = [hourdeg, mindeg, secdeg];
      //线宽
      ctx.lineWidth = 5;
      //绘制圆形的结束线帽
      ctx.lineCap = "round";
      for (let i = 0; i < 3; i++) {
        //beginPath() 方法开始一条路径，或重置当前的路径。
        ctx.beginPath();
        //arc(x, y, r, 起始角, 结束角);
        ctx.arc(100, 100, 70 + i * 10, 0, Math.PI * 2);
        //strokeStyle 属性设置或返回用于笔触的颜色、渐变或模式.
        ctx.strokeStyle = "#222";
        //stroke() 方法会实际地绘制出通过 moveTo() 和 lineTo() 方法定义的路径。默认颜色是黑色
        ctx.stroke();
        //方法开始一条路径
        ctx.beginPath();
        ctx.arc(
          100,
          100,
          70 + i * 10,
          //调整起始位置，逆时针旋转90°
          -Math.PI / 2,
          (Math.PI / 180) * lines[i] - Math.PI / 2
        );
        ctx.strokeStyle = colors[i];
        ctx.stroke();
      }
      //刻度绘制
      for (let j = 0; j < 12; j++) {
        let bx = 100 + 90 * Math.cos((j * 30 * Math.PI) / 180);
        let by = 100 + 90 * Math.sin((j * 30 * Math.PI) / 180);
        ctx.fillStyle = "#6fd08c";
        ctx.beginPath();
        ctx.arc(bx, by, 3, 0, Math.PI * 2);
        //方法填充当前的图像（路径）。默认颜色是黑色
        ctx.fill();
      }
      //设置阴影跟随秒针转动
      var x = 8 * Math.cos(((secdeg + 90) * Math.PI) / 180);
      var y = 8 * Math.sin(((secdeg + 90) * Math.PI) / 180);
      clock.style.textShadow =
        x + "px " + y + "px 3px hsl(" + (mindeg | 0) + ",30%,30%)";
      clock.style.color = "hsl(" + (mindeg | 0) + ",80%,50%)";
      clock.innerText =
        ("0" + d.getHours()).slice(-2) +
        ":" +
        ("0" + d.getMinutes()).slice(-2) +
        ":" +
        ("0" + d.getSeconds()).slice(-2);
      requestAnimationFrame(draw);
    }
    draw();
  })
}