-->
<template>
  <h3>1.圆环时钟</h3>
  <br /><br />
  <div class="timer">
    <canvas id="canvas"></canvas>
    <div id="clock"></div>
  </div>
  <br /><br />
  <h3>2.粒子时钟</h3>
  <br /><br />
  <div>
    <canvas class="particleClock"></canvas>
    <canvas class="particleClock"></canvas>
    <canvas class="particleClock"></canvas>
    <canvas class="particleClock"></canvas>
    <canvas class="particleClock"></canvas>
    <canvas class="particleClock"></canvas>
  </div>
  <br /><br />
  <h3>3.指针时钟</h3>
  <div class="pointerClock">
    <span>12</span><span>3</span><span>6</span><span>9</span>
    <div class="clock">00:00:00</div>
    <div class="hour"></div>
    <div class="min"></div>
    <div class="sec"></div>
    <div class="cir"></div>
  </div>
  <br /><br />
  <h3>4.简易时钟</h3>
  <br /><br />
  <div class="simpeClock">
    <div>
      <ul>
        <li>0</li>
        <li>0</li>
      </ul>
      <ul>
        <li>0</li>
        <li>0</li>
      </ul>
    </div>
    :
    <div>
      <ul>
        <li>0</li>
        <li>0</li>
      </ul>

      <ul>
        <li>0</li>
        <li>0</li>
      </ul>
    </div>
    :
    <div>
      <ul>
        <li>0</li>
        <li>0</li>
      </ul>
      <ul>
        <li>0</li>
        <li>0</li>
      </ul>
    </div>
  </div>
</template>
<script>
import useParticleClock from "../hooks/useParticleClock.js";
import useCircularClock from "../hooks/useCircularClock.js";
import usePointerClock from "../hooks/usePointerClock";
import useSimpleClock from "../hooks/useSimpleClock";
export default {
  name: "clockShow",
  setup() {
    let circularClock = useCircularClock();
    let particleClock = useParticleClock();
    let pointerClock = usePointerClock();
    let simpleClock = useSimpleClock();
    return {
      circularClock,
      particleClock,
      pointerClock,
      simpleClock,
    };
  },
};
</script>

<style>
.timer {
  display: flex;
  justify-content: center;
  align-items: center;
}
#clock {
  position: absolute;
  font-size: 20px;
  font-weight: bold;
}

.pointerClock {
  width: 240px;
  height: 240px;
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, 0);
  background: #2f3542;
  border-radius: 50%;
  box-shadow: 0 0 15px #1e90ff;
}
.pointerClock span {
  width: 90%;
  height: 90%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  color: white;
  font-size: 25px;

  text-shadow: 0 0 5px black;
}
.pointerClock span:nth-child(1) {
  align-items: flex-start;
  justify-content: center;
}
.pointerClock span:nth-child(2) {
  align-items: center;
  justify-content: flex-end;
}
.pointerClock span:nth-child(3) {
  align-items: flex-end;
  justify-content: center;
}
.pointerClock span:nth-child(4) {
  align-items: center;
  justify-content: flex-start;
}
.hour {
  width: 8px;
  height: 30%;
  background: #ced6e0;
  left: 50%;
  top: 20%;
  position: absolute;
  border-radius: 4px;
  transform-origin: center bottom;
  box-shadow: -2px -2px 5px black;
  transform: translateX(-50%);
}
.min {
  width: 4px;
  height: 35%;
  background: #1e90ff;
  left: 50%;
  top: 15%;
  position: absolute;
  border-radius: 2px;
  transform-origin: center bottom;
  box-shadow: -2px -2px 5px black;
  transform: translateX(-50%);
}
.sec {
  width: 2px;
  height: 40%;
  background: #ff4757;
  left: 50%;
  top: 10%;
  position: absolute;
  border-radius: 1px;
  transform-origin: center bottom;
  box-shadow: -2px -2px 5px black;
  transform: translateX(-50%);
}
.cir {
  width: 30px;
  height: 30px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background: #1e90ff;
  box-shadow: 0 0 5px black;
}
.clock {
  position: absolute;
  font-size: 20px;
  left: 50%;
  top: 60%;
  transform: translateX(-50%);
  color: #1e90ff;
  background: black;
  padding: 5px;
  border-radius: 5px;
}

.simpeClock {
  font-size: 120px;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  display: flex;
  line-height: 200px;
  color: white;
}
.simpeClock div {
  width: 240px;
  height: 200px;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
}

.simpeClock ul {
  width: 120px;
  position: relative;
  transition: 0.5s;
  top: 0px;
}

.simpeClock li {
  width: 120px;
  height: 200px;
  box-sizing: border-box;
  background: black;
  border-radius: 15px;
  border: 8px solid #333;
}
</style>